import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import SendIcon from '@material-ui/icons/Send';
import { addSentence } from '../actions/demoActions';
import { connect } from  'react-redux';

const styles = theme => ({
  container: {
    width: "100%",
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: "80%",
  },
  icon: {
    // margin: theme.spacing.unit,
  },
});

const mapStateToProps = (state) => {
  return {
    input: state.demo.input,
    lang: state.demo.lang,
  }
}

const mapDispatchToProps = dispatch => ({
  addNewSentence: (sentence) => dispatch(addSentence(sentence)),
});

const InputSentences = (props) => {
  const { input, lang } = props;
  return input.map((data, index) => {
    if (data.found) {
      const { extract, sentence } = data;
      if (extract && extract.length > 0) {
        const { startIndex, endIndex } = extract[0];
        return (
          <p key={`input${index}`}>
            {sentence.substring(0, startIndex)}
            <font color="red">
              <b>
                {sentence.substring(startIndex, endIndex + 1)}
              </b>
            </font>
            {sentence.substring(endIndex + 1)}
          </p>
        )
      }
    }
    return (<p key={`input${index}`}>{data.sentence} <b>({lang === 'en' ? 'Cannot find Food related word in sentence' : '找不到食物相關詞'})</b></p>)
  })
};

export class DemoBoard extends React.PureComponent {
  state = {
    'sentence': '',
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  onKeyPress = (event) => {
    const { addNewSentence } = this.props;
    const { sentence } = this.state;
    if (event.key === 'Enter' && sentence !== '') {
      addNewSentence(sentence);
      this.setState({'sentence': ''});
    }
    return false;
  };

  onClick = (event) => {
    const { addNewSentence } = this.props;
    const { sentence } = this.state;
    if (sentence !== '') {
      addNewSentence(sentence);
      this.setState({'sentence': ''});
    }
  };

  render() {
    const { classes } = this.props;
    let { input, lang } = this.props;
    // const title = lang === 'en' ? 'We extract Food related Keyword from your sentences' : '我們將會抽出句子中與食物的相關詞語';
    const title = lang === 'en' ? 'We extract Food related Keyword from your sentences' : '我们将会抽出句子中与食物的相关词语';
    // const placeHolder = lang === 'en' ? 'Please input your sentence here' : '請輸入句子';
    const placeHolder = lang === 'en' ? 'Please input your sentence here' : '请输入句子';
    return (
      <div className={classes.container}>
        <Typography variant="display1" component="h2" className={classes.textCen}>
          {title}
        </Typography>
        <TextField
          id="input"
          placeholder={placeHolder}
          value={this.state.sentence}
          className={classes.textField}
          onChange={this.handleChange('sentence')}
          onKeyPress={this.onKeyPress}
          margin="normal"
        />
        <Button variant="contained" color="primary" className={classes.button} onClick={this.onClick}>
          <SendIcon className={classes.icon} />
        </Button>
        <InputSentences input={input} lang={lang} />
      </div>
    );
  }
}

const StyledDemoBoard = withStyles(styles)(DemoBoard);
export default connect(mapStateToProps, mapDispatchToProps)(StyledDemoBoard);


